<template>
  <div class="container">
    <h1>Notas | {{numNotas}}</h1>
    <!-- ALERTS -->
    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
    >{{mensaje.texto}}</b-alert>
    <!-- FORM UPDATE -->
      <form @submit.prevent="editarNota(notaEditar)" v-if="editar" class="mb-3">
      <h3 class="text-center">Editar nota - {{idEdit}}</h3>
      <input
        type="text"
        placeholder="Ingrese un Nombre"
        class="form-control my-2"
        v-model="notaEditar.nombre"
      >
      <input
        type="text"
        placeholder="Ingrese una descripcion"
        class="form-control my-2"
        v-model="notaEditar.descripcion"
      >
      <b-button
        class="btn-sm btn-info mr-2"
        pill
        type="submit"
        :disabled="validaCampos(2) ? true : false"
      >Acalizar</b-button>
      <b-button
        class="btn-sm btn-secondary"
        pill
        type="submit"
        @click="editar=false"
      >Cancelar</b-button>
    </form>
    <!-- FORM CREATE-->
    <!-- privent doesn't upload the site -->
    <form @submit.prevent="agregarNota()" v-if="!editar" class="mb-3">
      <h3 class="text-center">Agregar nueva Nota</h3>
      <input
        type="text"
        placeholder="Ingrese un Nombre"
        class="form-control my-2"
        v-model="nota.nombre"
      >
      <input
        type="text"
        placeholder="Ingrese una descripcion"
        class="form-control my-2"
        v-model="nota.descripcion"
      >
      <b-button
        class="btn-sm btn-block btn-success"
        pill
        type="submit"
        :disabled="validaCampos(1) ? true : false"
      >Agregar</b-button>
    </form>
    <!-- TABLE -->
    <div>
      <table class="table">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Nombre</th>
            <th scope="col">Descripción</th>
            <th scope="col">Fecha</th>
            <th scope="col">Acciones</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in notas" :key="index">
            <th scope="row">{{item._id}}</th>
            <td>{{item.nombre}}</td>
            <td>{{item.descripcion}}</td>
            <td>{{item.date}}</td>
            <td>
              <b-button
                pill
                class="btn-warning btn-sm mx-2"
                @click="activarEdicion(item._id)"
              >Actualizar</b-button>
              <b-button pill class="btn-danger btn-sm mx-2" @click="eliminarNota(item._id)">Eliminar</b-button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      notas: [],
      mensaje: { color: "success", texto: "" },
      dismissSecs: 5,
      dismissCountDown: 0,
      nota: { nombre: "", descripcion: "" }, //Obj to send to the DB,
      editar: false,
      idEdit:null,
      notaEditar:{}
    };
  },
  created() {
    this.listarNotas();
  },
  methods: {
    listarNotas() {
      this.axios //This consumes the api customed 'localhos:3000/api'
        .get("/nota")
        .then(response => {
          //   console.log(response.data);
          this.notas = response.data;
        })
        .catch(e => {
          console.log("error" + e);
        });
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    },
    agregarNota() {
      this.axios
        .post("/nueva-nota", this.nota) //We consume the api for saving a note in DB
        .then(res => {
          // This add at first of the array notes
          this.notas.push(res.data);
          this.nota.nombre = "";
          this.nota.descripcion = "";
          console.log(this.nota);
          // Messages alerts in success case
          this.mensaje.texto = "!Nota Agregada!";
          this.mensaje.color = "success";
          this.showAlert();
        })
        .catch(e => {
          // Messages alert in error case
          console.log(e.response);
          if (e.response.data.error.errors.nombre.message)
            this.mensaje = e.response.data.error.errors.nombre.message;
          this.mensaje.color = "danger";
          this.mensaje.texto = "Hubo un error al guradar";
          this.showAlert();
        });
    },
    validaCampos(validacion) {
      let band = false;
      switch (validacion) {
        case 1:
          if (this.nota.descripcion == "" || this.nota.nombre == "")
          band = true;
          break;
        case 2:
          if (this.notaEditar.descripcion == "" || this.notaEditar.nombre == "")
          band = true;
          break;
      }
     return band;
    },
    activarEdicion(id) {
      // This actives the form
      this.editar = true;
      this.idEdit = id;
      // This does the request to the backend
      this.axios
        .get(`/nota/${id}`) //This brings the data from the bakend
        .then(res => {
          this.notaEditar = res.data; //This consume the api from bakend
        })
        .catch(e => {
          console.log(e.response);
        });
    },
    editarNota(item){
      this.axios.put(`/nota/${item._id}`,item)
      .then(res =>{
        const index = this.notas.findIndex(n => n._id === res.data._id);
        // Update the data beacase res returns the updated data
        this.notas[index].nombre = res.data.nombre;
        this.notas[index].descripcion = res.data.descripcion;
        // This puts the alerts
        this.mensaje.texto="¡Nota actualizada!";
        this.mensaje.color = "info";
        this.editar=false;        
      })
      .catch(e => {
        console.log(e);
        // This puts the alerts
        this.mensaje.texto="¡Error al actualizar!";
        this.mensaje.color = "warning";
      });
      this.showAlert();
    },
    eliminarNota(id) {
      //this.axios.<verbo http>
      this.axios
        .delete(`/nota/${id}`)
        .then(res => {
          //This find a item in notas and save it in index
          let index = this.notas.findIndex(item => item._id === res.data._id);
          this.notas.splice(index, 1); //This delete index from notas in the front
          //Messages alert
          this.mensaje.texto = "¡Nota Eliminada!";
          this.mensaje.color = "success";
          this.showAlert();
        })
        .catch(e => {
          console.log(e.response);
        });
    }
  },
  computed: {
    numNotas() {
      return this.notas.length;
    }
  }
};
</script>